.accordion {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 13px 0;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid #EBEAE8; }
  .accordion_link {
    text-transform: uppercase;
    display: flex; }
  .accordion__block {
    display: flex;
    justify-content: center;
    background: #fff;
    flex-direction: column;
    align-items: flex-start; }
  .accordion_content {
    position: relative;
    width: 100%;
    box-shadow: unset;
    transition: all .2s;
    visibility: hidden;
    opacity: 0;
    max-height: 1px;
    height: auto;
    display: flex;
    flex-direction: column;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 4px;
    background: #fff;
    z-index: -1; }
    .accordion_content_item {
      font-size: 14px;
      line-height: 130%;
      color: #453F3A;
      margin-top: 12px; }
      .accordion_content_item:hover {
        color: #A46C46; }
      .accordion_content_item.active {
        color: #A46C46; }
    .accordion_content__block {
      position: relative; }
      .accordion_content__block::after {
        content: '';
        position: absolute;
        width: 13px;
        right: 14px;
        height: 1px;
        background: #000;
        top: 20px; }
      .accordion_content__block::before {
        transition: all .2s;
        content: '';
        position: absolute;
        width: 1px;
        right: 20px;
        height: 12px;
        background: #000;
        top: 15px; }
      .accordion_content__block.active .accordion_content {
        top: 0;
        opacity: 1;
        z-index: 9;
        visibility: visible;
        max-height: 1000px; }
      .accordion_content__block.active .accordion__title {
        color: #A46C46; }
      .accordion_content__block.active::before {
        opacity: 0; }
