.blog {
  display: flex;
  width: 100%; }
  .blog_item {
    display: block;
    transition: all 0.3;
    width: 32%;
    margin: 1%; }
    .blog_item:hover {
      transform: scale(1.02); }
    .blog_item_img {
      width: 100%;
      height: 55%;
      max-height: 300px; }
      .blog_item_img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 8px; }
    .blog_item_info {
      padding: 0 5px; }
    .blog_item_title {
      margin: 16px 0 0;
      color: #453f3a;
      font-size: 22px;
      line-height: 130%;
      font-weight: 400; }
    .blog_item_text {
      font-size: 20px;
      line-height: 130%;
      color: #89817c;
      margin-top: 8px; }
