.cart_item {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 312px;
  width: 25%;
  padding: 1%; }
  .cart_item_wrapper {
    display: flex;
    flex-wrap: wrap; }
  .cart_item_img {
    width: 100%;
    height: 420px;
    text-align: center; }
    .cart_item_img img {
      object-fit: cover;
      width: 100%;
      height: 95%;
      max-width: 365px; }
  .cart_item_new {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 10px;
    width: 100px;
    background: #a46c46;
    border-radius: 10px 0px;
    font-size: 16px;
    line-height: 21px; }
  .cart_item_price {
    font-size: 20px;
    line-height: 26px;
    margin-right: 12px; }
    .cart_item_price__block {
      position: relative;
      align-items: center;
      display: flex; }
  .cart_item_procent {
    font-size: 16px;
    line-height: 21px;
    margin-right: 12px;
    display: flex;
    align-items: center; }
  .cart_item_old_price {
    font-size: 16px;
    line-height: 21px;
    /* identical to box height */
    text-decoration-line: line-through; }
  .cart_item_add_to_favorite {
    cursor: pointer;
    transition: all 0.2s;
    position: absolute;
    top: -9px;
    right: 0;
    opacity: 0.6;
    transform: scale(1);
    padding: 10px; }
    .cart_item_add_to_favorite path {
      transition: all 0.2s;
      fill: #89817c; }
    .cart_item_add_to_favorite:hover {
      opacity: 1;
      transform: scale(1.1); }
      .cart_item_add_to_favorite:hover path {
        fill: #a46c46; }
    .cart_item_add_to_favorite.active {
      opacity: 1;
      transform: scale(1.1); }
      .cart_item_add_to_favorite.active path {
        fill: #a46c46; }
  .cart_item_title {
    font-size: 22px;
    line-height: 130%;
    margin: 10px 0; }
  .cart_item_size {
    cursor: pointer;
    transition: all 0.2s;
    background: #fffcf2;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin: 4px;
    border-radius: 50px;
    width: 80px;
    font-size: 16px;
    line-height: 130%; }
    .cart_item_size.active, .cart_item_size:hover {
      background: #89817c;
      color: #fff; }
    .cart_item_size__block {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      margin: 0 -4px; }
