.footer {
  background-color: #F4F3EE;
  background-repeat: no-repeat;
  background-position: center; }
  .footer__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 5% 0; }
  .footer_form {
    width: 20%; }
    .footer_form_title {
      margin-bottom: 20px; }
    .footer_form_input {
      margin: 30px 0 12px 0; }
    .footer_form [type="submit"] {
      margin-top: 20px; }
  .footer__right {
    display: flex; }
  .footer__column {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
    .footer__column:first-child {
      margin-right: 100px; }
    .footer__column a img {
      margin-right: 20px; }
  .footer__links {
    display: flex;
    flex-direction: column; }
    .footer__links a {
      color: #89817C;
      margin-bottom: 30px; }
      .footer__links a:hover {
        color: #a46c46; }
  .footer .accept {
    color: #89817C;
    border-bottom: 1px solid #89817C;
    font-size: 14px; }
